@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Kufi+Arabic&family=Noto+Sans+Arabic&display=swap");

@font-face {
  font-family: "Uthmani";
  src: url("./assets/fonts/Uthmani.otf");
}

@font-face {
  font-family: "Quranic";
  src: url("./assets/fonts/quranic.ttf");
}

body {
  background-color: #152238;
  user-select: none;
  unicode-bidi: bidi-override !important;
}
.ayat {
  word-break: break-word;
  white-space: pre-line;
}
